import axios from 'axios';

const meetingsURL = 'https://open.auxgroup.com/api/auxhr/meetingrate/meetings/'

const participantsURL = 'https://open.auxgroup.com/api/auxhr/meetingrate/participants/'
//const ServerAddress = 'http://localhost:9999/meetings/'

export default {

  meetingList(){
    return axios.get(meetingsURL+'display');
  },

  participantsList(meetingId){
  	return axios.get(participantsURL+'meetings/'+meetingId+'/display');
  }

}


