<template>
<div class="panel">
    
    <div class="page__bd">
        
        <div class="weui-panel weui-panel_access" v-for="item in participantsList">

              <div class="weui-panel__bd">

                    <div class="weui-panel weui-panel_access">
                    <div class="weui-panel__bd">
                      <router-link :to="getUrl(item)" class="weui-media-box weui-media-box_appmsg">
                      <div class="weui-media-box__bd">
                        <h4 class="weui-media-box__title">No. {{item.partino}}</h4>
                        <h4 class="weui-media-box__title expireSafe">{{item.author}} {{item.authorStationLevel}}职等
                        </h4>
                        <ul class="weui-media-box__info">
                          <li class="weui-media-box__info__meta">摘要：</li>
                        </ul>
                        <p class="weui-media-box__desc">{{item.msg}}</p>
                          
                        
                      </div>

                      </router-link>

                      </div> <!--end weui-panel__bd -->
                    </div>
              </div><!--weui-panel__bd-->
         </div>        
    

        
    </div>
</div>
</template>

<script>
import server from '../../scripts/display.js'


export default {

    name: 'participantsList',
    data() {
        return {
            meetingId : this.$route.query.meetingId,
            participantsList: [],
        };
    },
    mounted() {

        var this2 = this;

        server.participantsList(this.meetingId).then( function(response){
            console.log(response.data);
            this2.participantsList = response.data;
        });


        // server.meetingList().then(function(response){} {

        //     this2.meetingList = response.data;

        // }).catch(function(error) {

        //     console.log(error);
        
        // });
    },

    methods: {

        getUrl: function(item) {
            //return '/ratermain?meetingId=' + item.meetingId +'&ratelevel='+item.ratelevel;
            return '/display/participant?id='+item.participantsId;
        }
    },

    computed: {

    }
};
</script>

<style scoped>

  .expireSafe{
    color:green;
  }

</style>



